    renderImages(images, true); // –î–æ–¥–∞—î–º–æ –Ω–æ–≤—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–æ –≥–∞–ª–µ—Ä–µ—ó
